<template>
  <div id="app">
    <div>
		<div>
			<el-divider content-position="center" ><p style="color: #c5c5c5;"></p></el-divider>
		</div>
		<el-button type="primary" @click="start"></el-button>
		<el-button type="primary" @click="stop"></el-button>
		<el-divider content-position="left"><h3></h3></el-divider>
		<div style="min-height: 150px;">
			 <el-tag >{{name}}</el-tag>
			<el-tooltip  v-for="(item,index) in isOkList" :key="index" class="item" effect="dark" :content="item.xh" placement="top-start">
				  <el-tag>{{item.name}}</el-tag>
			</el-tooltip>
		</div>

		<div>
			<el-divider content-position="left"><h4></h4></el-divider>
		</div>
		 <el-table
		      :data="list"
		      style="width: 100%">
		      <el-table-column
			   align="center"
		        prop="name"
		        label=""
		        width="180">
		      </el-table-column>
		      <el-table-column
			   align="center"
		        prop="xh"
		        label=""
		      >
		      </el-table-column>
		    </el-table>
	</div>

  </div>
</template>

<script>	
export default {
	data(){
		return {
			name:"",
			xh:null,
			i:0,
			list:[{
					name:"",
					xh:"2018212209"
				},{
					name:"",
					xh:"2018212204"
				},{
					name:"",
					xh:"2018212207"
				},{
					name:"",
					xh:"2018212179"
				},{
					name:"",
					xh:"2018212205"
				}],
			isOkList:[],
			isStart:false,
			ks:"",
		}
	},
  mounted(){
	this.initData();  
  },
  methods: {
	stop(){
		this.list.splice(this.i,1)
		clearInterval(this.ks);
		if(this.name == ""){
			 this.$message( {
				message: '',
				type: 'error',
				duration:"1000"
			 }
			);
			 return 0;
		}
		this.isOkList.push({
			name:this.name,
			xh:String(this.xh)
		})
		this.name="";
		this.xh="";
	},
	cj(){
		let i = Math.ceil(Math.random()*this.list.length-1); 
		this.name=this.list[i].name;
		this.xh=this.list[i].xh;
		this.i=i;
	},
	start(){
		
		clearInterval(this.ks);
		this.ks = setInterval(this.cj,10);
		console.log(this.ks)
	},
	initData(){
		this.$axios.get('http://wxzx.cqyti.com:8222/getList').then((response) => {
			this.list = response.data;
		})
	}
  }
}
</script>

<style scoped>
#app {
  font-family: Helvetica, sans-serif;
  text-align: center;
}
.el-tag{
	font-size: 30px;
	padding: 30px 20px;
	line-height: 0px;
}
.el-tag + .el-tag {
    margin-left: 10px;
  }
</style>



